@charset 'utf-8';

// Librairies
@import "fork-awesome";

// Administration de Wagtail
// -----------------------------------------------------------------------------

/// Permets l'utilisation de Fork Awesome pour les icones.
.icon-fa::before {
  font-family: "#{$fa-font-family}";
}

/// Permets de masquer l'icone d'un `.tag`.
.tag.no-icon::before {
  display: none;
}
